function cards() {
    
    class MenuCart {
        constructor(src, alt, title, descr, price, parentSelector, ...classes) {
            this.src = src,
            this.alt = alt,
            this.title = title,
            this.descr = descr,
            this.price = price,
            this.classes = classes,
            this.parent = document.querySelector(parentSelector),
            this.transfer = 27,
            this.changeToUAN();
        }

        changeToUAN() {
            this.price = this.price * this.transfer;
        }

        render() {
            const element = document.createElement('div');
            if(this.classes.length === 0) {
                this.element = 'menu__item';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }

            element.innerHTML = `
                <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.title}</h3>
                <div class="menu__item-descr">${this.descr}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">Цена:</div>
                    <div class="menu__item-total"><span>${this.price}</span> грн.</div>
                </div>
            `;

            this.parent.append(element);
        }
    }

    const getResource = async(url) => {    // создаем ф-ю GET запроса
        const res = await fetch(url);

        if(!res.ok) { // new function .OK - когда все хорошо
            throw new Error(`Could not fetch ${url}, status: ${res.status}`);   //THROW - выкидывание ошибки. NEW ERROR - сама ошибка
        }

        return await res.json();   // возвращаем результат в формате json
    };

    axios.get('http://localhost:3000/menu')   // интеграция библиотеки AXIOS в код, для работы с сервером
        .then(data => {
            data.data.forEach(({img, altimg, title, descr, price}) => {    // перебераем каждый элемент обьекта из сервера (деструктуризация объекта)
                 new MenuCart(img, altimg, title, descr, price, '.menu .menu-container').render();
            });
        });
}

export default cards;